:root {
  --darker: rgb(0, 4, 58);
  --dark: rgb(0, 13, 190);
  --main: rgb(24, 40, 255);
  --light: rgb(103, 126, 255);
  --lighter: rgb(183, 194, 255);
}

html{
  font-size: 62.7%;
  scroll-behavior: smooth;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

* {
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;
  overflow: hidden;
}

h2{
  font-family: inherit;
  font-size: 2.7rem;
  color: var(--light);
  margin: 1.2rem;
}

.longer-h2{
  width: 100%;
}

.h2-input{
  font-family: inherit;
  font-size: 2.7rem;
  font-weight: 700;
  border: none;
  color: var(--light);
  margin: 1.2rem;
}

h3{
  font-family: inherit;
  font-size: 2rem;
  color: var(--dark);
  margin: 1.2rem 0;
}

path{
  transition: opacity .5s ease;
}

input{
  outline: none;
  border: none;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 300;
  border-bottom: 1px solid var(--main);
  padding: .7rem .2rem;
  margin: 1rem;
  color: black;
  width: 43%;
}

textarea{
  outline: none;
  border: 2px solid var(--darker);
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  font-size: 1.6rem;
  font-weight: 300;
  padding: 10px 10px;
  margin: 0;
  color: var(--dark);
  overflow-y: scroll;
  width: 95%;
  height: 150px;
}

.btn-box{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.btn-box2{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 60%;
}

button{
  outline: none;
  border: none;
  color:var(--main);
  background-color: white;
  border: 2px solid var(--main);
  border-radius: 50px;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 400;
  transition: 1s ease;
  padding: 3px 10px;
  margin: 0 1rem .5rem .5rem;
  cursor: pointer;
}
button:active{
  background-color: var(--light);
}

.reset{
  color: rgb(255, 145, 0);
  border-color: rgb(255, 145, 0);
}
.reset:active {
  background-color: rgb(255, 201, 131);
}

.save{
  color: rgb(149, 190, 0);
  border-color: rgb(149, 190, 0);
}
.save:active {
  background-color: rgb(178, 218, 0);
}

.hide{
  color: rgb(92, 92, 92);
  border-color: rgb(104, 104, 104);
}
.hide:active {
  background-color: rgb(119, 119, 119);
}

.remove{
  color: red;
  border-color: red;
}
.remove:active {
  background-color: rgb(255, 139, 139);
}

.animate{
  color: rgb(119, 0, 255);
  border-color: rgb(140, 0, 255);
}
.animate:active {
  background-color: rgb(180, 139, 255);
}

.show{
  color:tomato;
  border-color: tomato;
}
.show:active{
  background-color: rgb(255, 168, 153);
}

.genNew{
  color: rgb(0, 153, 255);
  border-color: rgb(0, 140, 255);
}
.genNew:active {
  background-color: rgb(139, 185, 255);
}

.coords-container{
  margin: 1rem 1rem 3rem 1rem;
  width: 100%;
  height: 180px;
}

.coords-heading {
  display: flex;
  align-items: center;
}

.copy{
  cursor: pointer;
  position: relative;
  font-size: 1.8rem;
  width: 27px;
  height: 27px;
  background-color: white;
  color: var(--main);
  border: none;
  border-radius: 5px;
  padding: .5rem;
}
.copy:hover{
  background-color: rgb(228, 230, 255);
}

.length-container{
  margin: 1rem;
}

.copyLength{
  top: 0px;
  left: 2px;
}
.reloadLength{
  top: 0px;
  left: -10px;
}
.copyCoords{
  top: 0px;
  left: 2px;
}

.length-heading {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.length-input{
  border: none;
  width: 98%;
  background-color: var(--darker);
  border-radius: 5px;
  color: white;
  font-family: monospace;
  padding: 10px 10px;
  margin: 0rem;
}

.faded{
  color: rgb(148, 148, 148);
  cursor: pointer;
}

#section{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  padding: 0px;
  align-items: flex-start;
  justify-content: space-between;
}

@media (max-width: 900px) {
  body{
      overflow-y: visible;
  }
  #section{
      flex-direction: column;
      height: 160vh;
  }
  #filler{
      width: 100% !important;
      height: 100vh !important;
  }
  .right-column{
      width: 95% !important;
      height: 400px !important;
      margin: 0 3rem;
  }
  .layers-container{
      margin-top: 3rem !important;
  }
  .btn-box-top{
      justify-content: flex-end;
  }
}

#heading{
  z-index: 100 !important;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 2rem;
}

#animateBtn{
  z-index: 100;
}
#addLayerBtn{
  z-index: 100;
}
#selectBtn{
  z-index: 100;
}

.canvas{
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0 ;
}

#canvas2{
  width: 55vw;
  height: 600px;
  top: 10vh;
  left: 5vw;
}

#canvas3{
  position: absolute;
  width: 800px;
  height: 500px;
  transform: scaleX(-1.7);
  top: -29vh;
  left: 25vw;
  z-index: 400;
  stroke-dasharray: 1112.2606201171875;
  stroke-dashoffset: -1112.2606201171875;
  animation: draw2 6s ease-out 0s 1 forwards;
}
@keyframes draw2 {
  0%{
      stroke-dashoffset: -1112.2606201171875;
  }
  100% {
      stroke-dashoffset: 0;
  }
}

.h3-margin-left{
  margin-left: 1rem;
}

.small-input{
  width: auto;
  height: 100%;
  font-family: monospace;
  color: white;
  opacity: 0%;
}

#filler{
  width: 70vw;
  height: 100%;
  transition: 1s ease;
}

.right-column{
  width: 40vw;
  height: 100vh;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  z-index: 100;
  display: none;
  display: flex;
}

#col-header{
  padding: 10px 10px 0 10px;
  width: 100%;
  height: 60px;
  position: relative;
  left: 0%;
  border-bottom: 2px solid var(--main);
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 0;
}

#tab-container{
  display: flex;
  justify-content: flex-start;
  width: 97%;
}

.tab{
  border-radius: 10px 25px 0px 0px;
  border-bottom: none;
  padding: 3px 25px 3px 10px;
  margin: 0px 5px;
  transition: 1s ease;
  font-size: 1.6em;
}

.clicked{
  background-color: var(--main);
  color: white;
}

.layers-container{
  width: 100%;
  height: 90vh;
  overflow-y: scroll;
  padding: 0px 10px;
  margin-top: 0;
}

.layer{
  box-shadow: 0px 5px 10px rgba(128, 128, 128, 0.719);
  padding: 15px 10px;
  margin: 10px 5px 20px 5px;
  border-radius: 10px;
  background-color: white;
  transition: 1s ease;
}

.saveLayer{
  height: 40%;
}

.draw{
  animation: daw 6s linear 0s 1 forwards;
}
@keyframes draw {
  0%{
      stroke-dashoffset: 2260.47705078125;
  }
  100% {
      stroke-dashoffset: 0;
  }
}

@keyframes scribble {
  0%{
      stroke-dashoffset: 0;
  }
  100% {
      stroke-dashoffset: 50470;
  }
}

.saved-path{
  opacity: 0%;
  transition: opacity .5s ease;
}